.gameListsWrapper {
	display: flex;
	justify-content: center;
}

h1.title {
	color: #CFE0F9;
}

.gameLists {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	cursor: pointer;
	width: 90vw;
}

.gameListCard {
	border: solid #CFE0F9 1px;
	background-color: #CFE0F9;
	margin: 0.5em;
	padding: 0.2em;
	border-radius: 1em;
	width: 20vw;
	overflow: hidden;
}

.gameListCard h1{
	color: #283654;
	font-family: 'Saira Semi Condensed', sans-serif;
	overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.gameListCard p{
	color: #283654;
	overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
	font-family: 'Roboto', sans-serif;
}

@media screen and (max-width:640px){
	.gameLists {
		width: 95vw;
	}
	.gameListCard {
		width: 35vw;
	}
	.gameListCard h1{
		font-size: 1.1em;
	}
}

